import bridge from "@vkontakte/vk-bridge";
//constants
import { GROUP_ID } from "../constants";

// —Ä–∞–∑—Ä–µ—à–µ–Ω–∏–µ –Ω–∞ –æ—Ç–ø—Ä–∞–≤–∫—É —Å–æ–æ–±—â–µ–Ω–∏–π –æ—Ç –∏–º–µ–Ω–∏ –≥—Ä—É–ø–ø—ã
export function subscribeMessageFromGroup(openAlertPay, setActiveTab) {
  bridge
    .send("VKWebAppAllowMessagesFromGroup", {
      group_id: GROUP_ID,
    })
    .then((res) => {
      // console.log("subscribeMessageFromGroup result", res);
    })
    .catch((err) => {
      // console.log("subscribeMessageFromGroup result", err);

      setActiveTab("buy");
      openAlertPay(
        `–ß—Ç–æ–±—ã –ø—Ä–æ–¥–æ–ª–∂–∏—Ç—å, —Ä–∞–∑—Ä–µ—à–∏—Ç–µ –æ—Ç–ø—Ä–∞–≤–∫—É —Å–æ–æ–±—â–µ–Ω–∏–π –æ—Ç –∏–º–µ–Ω–∏ –≥—Ä—É–ø–ø—ã`,
        "red"
      );
    });
}

// –ø–æ–¥–ø–∏—Å–∫–∞ –Ω–∞ –≥—Ä—É–ø–ø—É
export function addGroup(openAlertPay, setActivePanel) {
  console.log("add group");

  bridge
    .send("VKWebAppJoinGroup", { group_id: GROUP_ID })
    .then(({ result }) => {
      console.log("VKWebAppJoinGroup", result);
      if (result) {
        setActivePanel("home");
        openAlertPay("–í—ã —É–∂–µ –ø–æ–¥–ø–∏—Å–∞–Ω—ã –Ω–∞ –≥—Ä—É–ø–ø—É");
      }
    })
    .catch((err) => {
      openAlertPay("–ü–æ–¥–ø–∏—à–∏—Ç–µ—Å—å –Ω–∞ –≥—Ä—É–ø–ø—É, —á—Ç–æ–±—ã –ø—Ä–æ–¥–æ–ª–∂–∏—Ç—å", "red");
    });
}

// –¥–æ–±–∞–≤–ª–µ–Ω–∏–µ —Å–µ—Ä–≤–∏—Å–∞ –≤ —Å–æ–æ–±—â–µ—Å—Ç–≤–æ
export function AddToCommunity() {
  bridge
    .send("VKWebAppAddToCommunity", {})
    .then((res) => {
      if (res.group_id) {
        console.log("AddToCommunity res:", res.group_id);
        return true;
      }
    })
    .catch((err) => {
      console.log("AddToCommunity err:", err);
      return false;
    });
}

// –æ—Ç–∫—Ä—ã—Ç–∏–µ –¥—Ä –ø—Ä–∏–ª–æ–∂–µ–Ω–∏–µ
export function goToApp(appID) {
  bridge.send("VKWebAppOpenApp", { app_id: appID, location: "GLI" });
}
